const signInConfig = {
  client_id: process.env.REACT_APP_CLIENT_ID,
  redirect_uri: process.env.REACT_APP_REDIRECT_URI || "http://localhost:3000/",
  response_type: "token",
  scope: "user-library-read user-top-read user-read-recently-played playlist-read-private"
}

export const buildUrl = (base: string, params: { [key: string]: any }) => {
  const paramString = Object.entries(params).map(([key, value]) => {
    return `${encodeURIComponent(key)}=${encodeURIComponent(value)}`
  }).join('&')

  return `${base}?${paramString}`
}

export const initSignIn = () => {
  window.sessionStorage.clear()
  const signInUrl = buildUrl('https://accounts.spotify.com/authorize', signInConfig)
  window.location.href = signInUrl
}

export const getAuthToken = () => {
  return window.sessionStorage.getItem('access_token')
}

export const handleAuthToken = () => {
  const hashSlashIndex = window.location.href.indexOf('/#/')
  const startIndex = hashSlashIndex === -1 ? 0 : hashSlashIndex + 3
  const hashIndex = window.location.href.indexOf('#', startIndex)

  if (hashIndex !== -1) {
    const stripped = window.location.href.substring(0, hashIndex)

    window.location.href.substring(hashIndex + 1).split('&').forEach(param => {
      const [key, value] = param.split('=', 2)

      console.log('key', key)
      console.log('value', value)

      if (key === 'access_token' || key === 'token_type') {
        window.sessionStorage.setItem(key, value)
      } else if (key === 'expires_in') {
        const expiresAt = Date.now() + Number.parseInt(value, 10) * 1000
        window.sessionStorage.setItem('token_expires_at', expiresAt.toString())
      }
    })

    window.location.href = stripped
  }
}