import React from 'react'
import { Col } from 'react-bootstrap'

import Card from 'react-bootstrap/Card'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'

import { Artist, Paging } from 'spotify-types'

import { bestImageHeightMatcher, getTopArtists, TimeRange } from './lib/api'

import TimeRangeSelector from './TimeRangeSelector'

const bestImageHeight = bestImageHeightMatcher(160)

const TopArtists = () => {
  const [timeRange, setTimeRange] = React.useState<TimeRange>('long_term')
  const [error, setError] = React.useState<string | null>(null)
  const [loading, setLoading] = React.useState(true)
  const [data, setData] = React.useState<Paging<Artist> | null>(null)

  React.useEffect(() => {
    getTopArtists(timeRange).then((response) => {
      setData(response.data)
      setLoading(false)
      setError(null)
    }).catch(() => {
      setError('Failed  to fetch top artists')
    })
  }, [timeRange])

  return (
    <Container>
      <Row>
        <h1>Top Artists</h1>
      </Row>
      <Row>
        <Col>
          <TimeRangeSelector onChange={setTimeRange} value={timeRange} />
        </Col>
      </Row>
      <Row>
        {loading && <div>Loading&hellip;</div>}
        {error && <div>{error}</div>}
        {data?.items.map((artist, index) => {
          return (
            <Card key={artist.id} border="dark" style={{ margin: 16, padding: 4, width: 200 }}>
              <Card.Img variant="top" src={bestImageHeight(artist.images)} width={160} />
              <Card.Body>
                <Card.Title>{index + 1}: {artist.name}</Card.Title>
              </Card.Body>
            </Card>
          )
        })}
      </Row>
    </Container>
  )
}

export default TopArtists