import BsPagination from 'react-bootstrap/Pagination'

import { Paging } from 'spotify-types'

const currentCount = 19

const makePaginationItem = (page: number, current: number, setPage: (page: number) => void) => (
  <BsPagination.Item key={page} active={page === current} onClick={() => setPage(page * 1)}>
    {page + 1}
  </BsPagination.Item>
)

const Pagination = ({
  data,
  current,
  setPage,
}: {
  data: Paging<any> | null
  current: number
  setPage: (page: number) => void
}) => {
  const pages: React.ReactElement[] = []
  let maxPage = 0

  if (data) {
    var page
    maxPage = Math.floor(data.total / data.limit)

    if (maxPage < 20) {
      for (page = 0; page <= maxPage; page++) {
        pages.push(makePaginationItem(page, current, setPage))
      }
    } else {
      const currentPadding = Math.floor(currentCount / 2)

      const startPoint = Math.max(Math.min(current - currentPadding, maxPage - currentCount + 1), 0)
      const endPoint = Math.min(startPoint + currentCount - 1, maxPage)

      if (startPoint > 0) {
        pages.push(<BsPagination.Ellipsis />)
      }

      for (page = startPoint; page <= endPoint; page++) {
        pages.push(makePaginationItem(page, current, setPage))
      }

      if (endPoint < maxPage) {
        pages.push(<BsPagination.Ellipsis />)
      }
    }
  }

  return (
    <BsPagination>
      <BsPagination.First disabled={current === 0} onClick={() => setPage(0)}/>
      <BsPagination.Prev disabled={current === 0} onClick={() => setPage(current - 1)} />
      <BsPagination.Next disabled={current === maxPage} onClick={() => setPage(current + 1)} />
      <BsPagination.Last disabled={current === maxPage} onClick={() => setPage(maxPage)} />
      {' '}
      {pages}
    </BsPagination>
  )
}

export default Pagination
