import React from 'react'

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Table from 'react-bootstrap/Table'

import { Paging, SavedTrack } from 'spotify-types'

import Pagination from './Pagination'

import { getTracks } from './lib/api'

const Albums = () => {
  const [page, setPage] = React.useState(0)
  const [error, setError] = React.useState<string | null>(null)
  const [loading, setLoading] = React.useState(true)
  const [data, setData] = React.useState<Paging<SavedTrack> | null>(null)

  React.useEffect(() => {
    getTracks(page).then((response) => {
      setData(response.data)
      setLoading(false)
      setError(null)
    }).catch(() => {
      setError('Failed  to fetch tracks')
    })
  }, [page])

  return (
    <Container>
      <Row>
        <h1>Saved Tracks</h1>
      </Row>
      <Row>
        <Pagination current={page} setPage={setPage} data={data} />
      </Row>
      <Row>
        {loading && <div>Loading&hellip;</div>}
        {error && <div>{error}</div>}
        <Table>
          <thead>
            <tr>
              <th>Artist</th>
              <th>Title</th>
              <th>Added At</th>
            </tr>
          </thead>
          <tbody>
            {data?.items.map((track) => (
              <tr>
                <td>{track.track.artists.map(artist => artist.name).join(' / ')}</td>
                <td>{track.track.name}</td>
                <td>{track.added_at}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Row>
    </Container>
  )
}

export default Albums