import React from 'react'

import { Link } from 'react-router-dom'

import Container from 'react-bootstrap/Container'
import Image from 'react-bootstrap/Image'
import Row from 'react-bootstrap/Row'
import Table from 'react-bootstrap/Table'

import { Paging, SimplifiedPlaylist } from 'spotify-types'

import { bestImageHeightMatcher, getPlaylists } from './lib/api'

const bestImageHeight = bestImageHeightMatcher(60)

const Playlists = () => {
  const [error, setError] = React.useState<string | null>(null)
  const [loading, setLoading] = React.useState(true)
  const [data, setData] = React.useState<Paging<SimplifiedPlaylist> | null>(null)

  React.useEffect(() => {
    getPlaylists().then((response) => {
      setData(response.data)
      setLoading(false)
      setError(null)
    }).catch(() => {
      setError('Failed  to fetch playlists')
    })
  }, [])

  return (
    <Container>
      <Row>
        <h1>Playlists</h1>
      </Row>
      <Row>
        {loading && <div>Loading&hellip;</div>}
        {error && <div>{error}</div>}
        <Table>
          <thead>
            <tr>
              <th>&nbsp;</th>
              <th>Name</th>
              <th># Tracks</th>
            </tr>
          </thead>
          <tbody>
            {data?.items.map((playlist) => (
              <tr key={playlist.id}>
                <td><Image src={bestImageHeight(playlist.images)} height={60} /></td>
                <td><Link to={`/playlists/${playlist.id}`}>{playlist.name}</Link></td>
                <td>{playlist.tracks.total}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Row>
    </Container>
  )
}

export default Playlists