import React from 'react'

import Container from 'react-bootstrap/Container'
import Image from 'react-bootstrap/Image'
import Row from 'react-bootstrap/Row'
import Table from 'react-bootstrap/Table'

import { Paging, SavedAlbum } from 'spotify-types'

import Pagination from './Pagination'

import { bestImageHeightMatcher, getAlbums } from './lib/api'

const bestImageForHeight = bestImageHeightMatcher(60)

const Albums = () => {
  const [page, setPage] = React.useState(0)
  const [error, setError] = React.useState<string | null>(null)
  const [loading, setLoading] = React.useState(true)
  const [data, setData] = React.useState<Paging<SavedAlbum> | null>(null)

  React.useEffect(() => {
    getAlbums(page).then((response) => {
      setData(response.data)
      setLoading(false)
      setError(null)
    }).catch(() => {
      setError('Failed  to fetch albums')
    })
  }, [page])

  return (
    <Container>
      <Row>
        <h1>Saved Albums</h1>
      </Row>
      <Row>
        {loading && <div>Loading&hellip;</div>}
        {error && <div>{error}</div>}
        <Pagination data={data} current={page} setPage={setPage} />
        <Table>
          <thead>
            <tr>
              <th>&nbsp;</th>
              <th>Artist</th>
              <th>Title</th>
              <th>Added At</th>
            </tr>
          </thead>
          <tbody>
            {data?.items.map((album) => (
              <tr>
                <td><Image src={bestImageForHeight(album.album.images)} height={60} /></td>
                <td>{album.album.artists.map(artist => artist.name).join(' / ')}</td>
                <td>{album.album.name}</td>
                <td>{album.added_at}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Row>
    </Container>
  )
}

export default Albums