import React from 'react'

import { useParams } from 'react-router-dom'

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Table from 'react-bootstrap/Table'

import { Paging, PlaylistTrack } from 'spotify-types'

import Pagination from './Pagination'

import { getPlaylistTracks, playlistEntryIsTrack } from './lib/api'

const Playlist = () => {
  const { id } = useParams()

  const [page, setPage] = React.useState(0)
  const [error, setError] = React.useState<string | null>(null)
  const [loading, setLoading] = React.useState(true)
  const [data, setData] = React.useState<Paging<PlaylistTrack> | null>(null)

  React.useEffect(() => {
    if (id !== undefined) {
      getPlaylistTracks(id, page).then((response) => {
        setData(response.data)
        setLoading(false)
        setError(null)
      }).catch(() => {
        setError('Failed  to fetch playlist tracks')
      })
    }
  }, [id, page])

  return (
    <Container>
      <Row>
        <h1>Playlist Details</h1>
      </Row>
      <Row>
        {loading && <div>Loading&hellip;</div>}
        {error && <div>{error}</div>}
        <Pagination data={data} current={page} setPage={setPage} />
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>Artist</th>
              <th>Title</th>
            </tr>
          </thead>
          <tbody>
            {data?.items.map((entry, index) => {
              if (!playlistEntryIsTrack(entry.track)) {
                return null
              }

              return (
                <tr key={entry.track.id}>
                  <td>{page * data.limit + index + 1}</td>
                  <td>{entry.track.artists.map(artist => artist.name).join(' / ')}</td>
                  <td>{entry.track.name}</td>
                </tr>
              )
            })}
          </tbody>
        </Table>
      </Row>
    </Container>
  )
}

export default Playlist