import React from 'react'

import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Table from 'react-bootstrap/Table'

import { Paging, Track } from 'spotify-types'

import { getTopTracks, TimeRange } from './lib/api'
import TimeRangeSelector from './TimeRangeSelector'

const TopTracks = () => {
  const [timeRange, setTimeRange] = React.useState<TimeRange>('long_term')
  const [error, setError] = React.useState<string | null>(null)
  const [loading, setLoading] = React.useState(true)
  const [data, setData] = React.useState<Paging<Track> | null>(null)

  React.useEffect(() => {
    getTopTracks(timeRange).then((response) => {
      setData(response.data)
      setLoading(false)
      setError(null)
    }).catch(() => {
      setError('Failed  to fetch top tracks')
    })
  }, [timeRange])

  return (
    <Container>
      <Row>
        <h1>Top Tracks</h1>
      </Row>
      <Row>
        <Col>
          <TimeRangeSelector onChange={setTimeRange} value={timeRange} />
        </Col>
      </Row>
      <Row>
        {loading && <div>Loading&hellip;</div>}
        {error && <div>{error}</div>}
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>Artist</th>
              <th>Title</th>
            </tr>
          </thead>
          <tbody>
            {data?.items.map((track, index) => (
              <tr key={track.id}>
                <td>{index + 1}</td>
                <td>{track.artists.map(artist => artist.name).join(' / ')}</td>
                <td>{track.name}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Row>
    </Container>
  )
}

export default TopTracks