import React from 'react'

import Container from 'react-bootstrap/Container'
import Image from 'react-bootstrap/Image'
import Row from 'react-bootstrap/Row'

import { PrivateUser } from 'spotify-types'

import { getMyProfile } from './lib/api'

const Home = () => {
  const [error, setError] = React.useState<string | null>(null)
  const [loading, setLoading] = React.useState(true)
  const [data, setData] = React.useState<PrivateUser | null>(null)

  React.useEffect(() => {
    getMyProfile().then((response) => {
      setData(response.data)
      setLoading(false)
      setError(null)
    }).catch(() => {
      setError('Failed to fetch profile')
    })
  }, [])

  return (
    <Container>
      <Row>
        {loading && <div>Loading&hellip;</div>}
        {error && <div>{error}</div>}
        <h1>Hello, {data?.display_name}</h1>
        {data && data.images.length > 0 && (
          <div>
            <Image roundedCircle src={data.images[0].url} height={80} />
            <p>Looking good...</p>
          </div>
        )}
      </Row>
    </Container>
  )
}

export default Home
