import { Outlet, Route, Routes } from 'react-router-dom'
import { LinkContainer } from 'react-router-bootstrap'
import Container from 'react-bootstrap/Container'
import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'

import 'bootstrap/dist/css/bootstrap.min.css'

import SignIn from './SignIn'

import Albums from './Albums'
import Home from './Home'
import Playlist from './Playlist'
import Playlists from './Playlists'
import TopArtists from './TopArtists'
import TopTracks from './TopTracks'
import Tracks from './Tracks'

function App() {
  const signedIn = !!window.sessionStorage.getItem('access_token')

  return (
    <div>
      <header>
        <Navbar bg="dark" variant="dark">
          <Container>
            <Navbar.Brand href="#home">Spotify Browser</Navbar.Brand>
            <Nav className="me-auto">
              <LinkContainer to="/"><Nav.Link>Home</Nav.Link></LinkContainer>
              <LinkContainer to="/playlists"><Nav.Link>Playlists</Nav.Link></LinkContainer>
              <LinkContainer to="/albums"><Nav.Link>Saved Albums</Nav.Link></LinkContainer>
              <LinkContainer to="/tracks"><Nav.Link>Save Tracks</Nav.Link></LinkContainer>
              <LinkContainer to="/top-artists"><Nav.Link>Top Artists</Nav.Link></LinkContainer>
              <LinkContainer to="/top-tracks"><Nav.Link>Top Tracks</Nav.Link></LinkContainer>
            </Nav>
          </Container>
        </Navbar>
      </header>
      <main>
        {signedIn ? (
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/albums" element={<Albums />} />
            <Route path="/playlists" element={<Playlists />} />
            <Route path="/playlists/:id" element={<Playlist />} />
            <Route path="/top-artists" element={<TopArtists />} />
            <Route path="/top-tracks" element={<TopTracks />} />
            <Route path="/tracks" element={<Tracks />} />
          </Routes>
        ) : <SignIn />}
        <Outlet />
      </main>
    </div>
  );
}

export default App;
