import axios, { AxiosResponse } from 'axios'
import { setup } from 'axios-cache-adapter'
import { Artist, Episode, Image, Paging, PlaylistTrack, PrivateUser, SavedAlbum, SavedTrack, SimplifiedPlaylist, Track } from 'spotify-types'
import { getAuthToken, initSignIn } from './auth'

const client = setup({
  baseURL: 'https://api.spotify.com/v1',
  cache: {
    maxAge: 15 * 60 * 1000
  }
})

export type TimeRange = 'short_term' | 'medium_term' | 'long_term'

const makeRequest = async (path: string, params: { [key: string]: any } = {}): Promise<AxiosResponse> => {
  const token = getAuthToken()

  if (token === null) {
    return Promise.reject("No token")
  }

  try {
    const response = await client.get(path, {
      headers: {
        'Authorization': `Bearer ${token}`,
      },
      params: params,
    })

    return response
  } catch (error) {
    if (axios.isAxiosError(error) && error.response && error.response.status === 401) {
      initSignIn()
    }

    return Promise.reject(error)
  }
}

export const playlistEntryIsTrack = (entry: Track | Episode | null): entry is Track => {
  return entry !== null && entry.type === 'track'
}

export const getAlbums = async (page: number = 0): Promise<AxiosResponse<Paging<SavedAlbum>>> => {
  return makeRequest('/me/albums', { limit: 50, offset: page * 50 })
}

export const getMyProfile = async (): Promise<AxiosResponse<PrivateUser>> => {
  return makeRequest('/me')
}

export const getPlaylistTracks = async (id: string, page: number = 0): Promise<AxiosResponse<Paging<PlaylistTrack>>> => {
  return makeRequest(`/playlists/${encodeURIComponent(id)}/tracks`, { limit: 50, offset: page * 50 })
}

export const getPlaylists = async (): Promise<AxiosResponse<Paging<SimplifiedPlaylist>>> => {
  return makeRequest('/me/playlists', { limit: 50 })
}

export const getTopArtists = async (timeRange: TimeRange): Promise<AxiosResponse<Paging<Artist>>> => {
  return makeRequest('/me/top/artists', { limit: 50, time_range: timeRange })
}

export const getTopTracks = async (timeRange: TimeRange): Promise<AxiosResponse<Paging<Track>>> => {
  return makeRequest('/me/top/tracks', { limit: 50, time_range: timeRange })
}

export const getTracks = async (page: number = 0): Promise<AxiosResponse<Paging<SavedTrack>>> => {
  return makeRequest('/me/tracks', { limit: 50, offset: page * 50 })
}

export const bestImageHeightMatcher = (height: number) => (images: Image[]) => {
  let best: Image | undefined

  for (const image of images) {
    if (
      best === undefined ||
      (image.height && best.height && Math.abs(image.height - height) < Math.abs(best.height - height))
    ) {
      best = image
    }
  }

  return best?.url
}